@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap');

:root {
  /* Light theme variables */
  --bg-color-light: #ffffff;
  --text-color-light: #1a1a1a;
  --card-bg-light: #f7f7f7;
  --border-color-light: #e0e0e0;
  --hover-color-light: #f0f0f0;
  --shadow-color-light: rgba(0, 0, 0, 0.05);
  
  /* Dark theme variables */
  --bg-color-dark: #000000;
  --text-color-dark: #ffffff;
  --card-bg-dark: #1c1c1e;
  --border-color-dark: #2c2c2e;
  --hover-color-dark: #2c2c2e;
  --shadow-color-dark: rgba(255, 255, 255, 0.05);
  
  /* Common variables */
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Default light theme */
:root[data-theme="light"] {
  --bg-color: var(--bg-color-light);
  --text-color: var(--text-color-light);
  --card-bg: var(--card-bg-light);
  --border-color: var(--border-color-light);
  --hover-color: var(--hover-color-light);
  --shadow-color: var(--shadow-color-light);
}

/* Dark theme */
:root[data-theme="dark"] {
  --bg-color: var(--bg-color-dark);
  --text-color: var(--text-color-dark);
  --card-bg: var(--card-bg-dark);
  --border-color: var(--border-color-dark);
  --hover-color: var(--hover-color-dark);
  --shadow-color: var(--shadow-color-dark);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  transition: var(--transition);
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 2rem;
}

.header {
  text-align: center;
  margin-bottom: 4rem;
  padding: 4rem 0;
  border-bottom: 1px solid var(--border-color);
  position: relative;
}

.theme-toggle {
  position: fixed;
  top: 2rem;
  right: 2rem;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  padding: 0.75rem;
  border-radius: 50%;
  cursor: pointer;
  transition: var(--transition);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 12px var(--shadow-color);
}

.theme-toggle:hover {
  transform: scale(1.1);
}

.name {
  font-size: 3.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  letter-spacing: -0.5px;
  background: linear-gradient(to right, var(--text-color) 0%, #666 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.title {
  font-size: 1.25rem;
  font-weight: 400;
  color: #666;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.8;
}

.kanban {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
  padding-bottom: 5rem;
}

.column {
  background: var(--bg-color);
  border-radius: 16px;
  padding: 1.5rem;
  backdrop-filter: blur(10px);
}

.column-header {
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 1.5rem;
  color: #666;
  font-weight: 600;
}

.card {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1rem;
  cursor: pointer;
  transition: var(--transition);
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px var(--shadow-color);
}

.card.expanded {
  background: var(--card-bg);
  margin: 1rem -1.5rem;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 12px 36px var(--shadow-color);
}

.card-title {
  font-size: 1.125rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.card-period {
  font-size: 0.875rem;
  color: #666;
  margin-bottom: 1rem;
}

.card-content {
  max-height: 0;
  opacity: 0;
  transition: var(--transition);
  overflow: hidden;
}

.card.expanded .card-content {
  max-height: 1000px;
  opacity: 1;
  margin-top: 1rem;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
}

.tag {
  font-size: 0.75rem;
  padding: 0.25rem 0.75rem;
  background: var(--bg-color);
  border: 1px solid var(--border-color);
  border-radius: 20px;
  color: #666;
  transition: var(--transition);
}

.tag:hover {
  background: var(--hover-color);
}

.contact-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--bg-color);
  padding: 1.5rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
  border-top: 1px solid var(--border-color);
  backdrop-filter: blur(10px);
  z-index: 1000;
}

.contact-link {
  color: var(--text-color);
  text-decoration: none;
  font-size: 0.875rem;
  transition: var(--transition);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
}

.contact-link:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px var(--shadow-color);
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }
  
  .header {
    padding: 2rem 0;
    margin-bottom: 2rem;
  }
  
  .name {
    font-size: 2.5rem;
  }
  
  .kanban {
    grid-template-columns: 1fr;
  }
  
  .card.expanded {
    margin: 1rem -1rem;
    padding: 1.5rem;
  }
  
  .theme-toggle {
    top: 1rem;
    right: 1rem;
  }
}
